export enum InvoiceStatus {
    created = 0,
    payed = 1,
    expired = 2,
    canceled = 3,
    latePay = 4,
    wrongPay = 5
}

export class InvoiceStats {

    public static paid = "zaplatena";
    public static created = "vystavena";
    public static expired = "po splatnosti";
    public static canceled = "storno";
    public static latePay = "zaplatena po splatnosti";
    public static wrongPay = "zla suma uhradena";
}

export const invoiceTypes = ["Zlohov faktra","Doklad o prijati platby","Faktura"];
export const invoiceStatuses = ["created","paid","expired","canceled","latePay","wrong_paid","off"];
export const invoiceStatesConst = [{label:InvoiceStats.created, value:InvoiceStatus.created},
                                    {label:InvoiceStats.paid, value:InvoiceStatus.payed},
                                    {label:InvoiceStats.expired, value:InvoiceStatus.expired},
                                    {label:InvoiceStats.canceled, value:InvoiceStatus.canceled},
                                    {label:InvoiceStats.latePay, value:InvoiceStatus.latePay},
                                    {label:InvoiceStats.wrongPay, value:InvoiceStatus.wrongPay},{label: "Vypnut", value: -1}];

export function getTranslation(status : string){

  switch(status) {
      case "payed":
      case "paid" : return {text : "Zaplaten", label:"label-success"};
      case "created" : return {text : "Vystaven", label:"label-info"};
      case "expired" : return {text : "Po splatnosti", label:"label-danger"};
      case "canceled" : return {text : "Stornovan", label:"label-default"};
      case "latePay" : return {text : "Zaplaten po splatnosti", label:"label-success"};
      case "wrong_paid" : return {text : "Zl suma uhraden", label:"label-danger"};
      default: return {text:"Undefined: " + status, label:"label-danger"};
  }
}

export function getInvoiceStatusFromEnum(status: InvoiceStatus): String{
    switch(status) {
        case InvoiceStatus.created : return "Vystaven";
        case InvoiceStatus.expired : return "Po splatnosti";
        case InvoiceStatus.canceled : return "Stornovan";
        case InvoiceStatus.latePay : return "Zaplaten po splatnosti";
        case InvoiceStatus.wrongPay : return "Zl suma uhraden";
        case InvoiceStatus.payed : return "Zaplaten";
    }
}


